import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { EventbusService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';

@Component({
  selector: 'app-admin-modal-container',
  standalone: true,
  imports: [],
  templateUrl: './admin-modal-container.component.html',
  styleUrl: './admin-modal-container.component.scss'
})
export class AdminModalContainerComponent
implements OnInit, OnDestroy {
  @Input() showModal = false;
  @Input({ required: true }) title!: string;
  @Input() message = '';
  @Input() cancelButton = true;
  @Input() cancelText = 'Cancel';
  @Input() confirmText = 'Confirm';
  @Input() confirmButon = false;
  @Input() showLoading = true;
  @Output() confirm: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() closed: EventEmitter<boolean> = new EventEmitter<boolean>();
  cleanup?: () => void;

  constructor(private eventbus: EventbusService) {}

  ngOnInit(): void {
    const handlers = {
      closeAdminModal: () => {
        this.showModal = false;
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  toggleModal() {
    this.showModal = !this.showModal;
    this.closed.emit(this.showModal);
  }

  onConfirm() {
    this.confirm.emit(true);
    this.toggleModal();
  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}
